export default {
	data() {
		return {
			userIsLogin:false,
			userinfo:{}
		}
	},
	onShow() {
		var me = this;
		//用户状态切换
		var userinfo = uni.getStorageSync("globalUser");
		//console.log(userinfo)
		if(userinfo != "" && userinfo != undefined){
			me.userIsLogin = true;
			me.userinfo = userinfo;
		}else{
			me.userIsLogin = false
			me.userinfo = {};
		}
	},
	methods: {
		
	}
}
